@import url(https://fonts.googleapis.com/css2?family=Cormorant+Garamond:ital,wght@0,400;0,600;1,300;1,400;1,600&display=swap);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
		'Droid Sans', 'Helvetica Neue', sans-serif;

	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
	text-align: center;
	width: 100vw;
	height: 100vh;
	background-size: cover;
	background-position: center;
	position: relative;
}

.outerdiv {
	width: 100vw;
	height: 100vh;
	/* display: flex; */
}

/* AUDIO CSS */
audio {
	width: 25vw;
	height: 50vh;
	max-height: 50px;
	margin-top: 10%;
	margin-right: 20%;
}

/*RUMI CSS */

.svgtext {
	font-family: 'Cormorant Garamond Bold', serif;
	color: rgb(102, 37, 155);
	font-size: 1.5vw;
}
#topcurve {
	width: 60%;
	height: 80%;
	position: absolute;
	-webkit-transform: translate(23%, 5%);
	        transform: translate(23%, 5%);
}
#svgidtop {
	position: absolute;
	top: 61vh;
	left: 9vw;
}

#svgidbottom {
	position: absolute;
	top: 70vh;
	left: 19vw;
}
#bottomcurve {
	width: 0%;
	height: 80%;
	position: absolute;
	-webkit-transform: translate(15%, 20%);
	        transform: translate(15%, 20%);
	left: 20vw;
}

.svgouterdive {
	position: relative;
}

/* APP - FACE MARKINGS */
#mouth_x {
	width: 13%;
	height: 13%;
	position: absolute;
	top: 67%;
	left: 34%;
}

#mouthxtorightid {
	stroke-dasharray: 160;
	stroke-dashoffset: 160;
	-webkit-animation: dash 1s linear forwards;
	        animation: dash 1s linear forwards;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease;
	        animation-timing-function: ease;
	-webkit-animation-direction: normal;
	        animation-direction: normal;
	-webkit-animation-delay: 7s;
	        animation-delay: 7s;
}

#mouthxtoleftid {
	stroke-dasharray: 141;
	stroke-dashoffset: 141;
	-webkit-animation: dash 1s linear forwards;
	        animation: dash 1s linear forwards;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease;
	        animation-timing-function: ease;
	-webkit-animation-direction: normal;
	        animation-direction: normal;
	-webkit-animation-delay: 9s;
	        animation-delay: 9s;
}
@-webkit-keyframes dash {
	to {
		stroke-dashoffset: 0;
	}
}
@keyframes dash {
	to {
		stroke-dashoffset: 0;
	}
}

#playbuttonsvgid {
	width: 5%;
	height: 5%;
	position: absolute;
	top: 23%;
	left: 34%;
}

#playbuttonpathid {
	stroke-dasharray: 199;
	stroke-dashoffset: 199;
	-webkit-animation: dash 3s linear forwards;
	        animation: dash 3s linear forwards;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease;
	        animation-timing-function: ease;
	-webkit-animation-direction: normal;
	        animation-direction: normal;
	-webkit-animation-delay: 1s;
	        animation-delay: 1s;
}

#eyestrokesid {
	width: 25%;
	height: 25%;
	position: absolute;
	top: 27%;
	left: 27%;
}

#eyestrokepathid {
	stroke-dasharray: 1637;
	stroke-dashoffset: 1637;
	-webkit-animation: dash 6s linear forwards;
	        animation: dash 6s linear forwards;
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	-webkit-animation-timing-function: ease;
	        animation-timing-function: ease;
	-webkit-animation-direction: normal;
	        animation-direction: normal;
	-webkit-animation-delay: 4s;
	        animation-delay: 4s;
}

.markings {
	transition: stroke .5s ease;
}
.markings:hover {
	stroke: rgb(201, 14, 14);
}

